.article
{
 /* background-color: #C8C8C8; */
 /* color: #323232; */
    /* min-height: 37.8vh; */
     background-color: #fff;
  border-radius: 8px;
  padding: 20px;
  margin: 20px 20px;
  box-shadow: 0 5px 4px rgba(0, 0, 0, 0.1); 
  border: 1px solid #313233; 
}
.article h2 {
    color: #333;
    margin-bottom: 15px;
    font-size: 1.5em;
}
.article p
{
    color: #666;
    line-height: 1.6;
    margin-bottom: 5px;
    /* margin: 0;
    padding: 10px; */
}
.calculation-section {
  background-color: #f9f9f9;
  padding: 15px;
  border-radius: 5px;
  margin-top: 5px;
}
.calculation-section h3 {
  margin-bottom: 15px;
  color: #333;
}
.input-group {
  margin-bottom: 5px;
}
.input-group label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
  color: #555;
}

.input-group input {
  width: 40%;
  text-align-last: center;
  /* background-color: #24282c; */
  /* color: #3fc3e4; */
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 14px;
}
.calculate-btn {
  background-color: #22383f;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  margin-top: 10px;
}